<template>
<!-- 新闻类别 -->
    <div class="news-types" v-if="data.length > 0">
        <div class="item"
         v-for="item in showChannels" :key="item.channels" 
         :class="{active: item.channelId === chooseId}" 
         @click="switchTo(item.channelId)" >
         <span class="name" >{{item.name}}</span>
         </div>
        <a href="" @click.prevent="isCollapse=!isCollapse">{{ isCollapse ? "展示":"收起"}}</a>
    </div>
</template>

<script>
import {getNewsChannels} from '../../services/newService'
import {mapState} from 'vuex'
export default {
    data(){
        return{
            
            isCollapse: true, //当前是否为折叠状态
            chooseId: null //当前选中的频道id
        };
    },
    computed:{
        ...mapState("channels",["data"]),
        //需要显示的频道
        showChannels(){
            if(this.isCollapse){
                //折叠状态
                return this.data.slice(0,8);// 折叠状态只取8个
            }else{
                //展开状态
                return this.data;
            }
        }
    },
    watch:{
        data:{
            immediate: true,
            handler(){
                if(this.data.length > 0){
                this.switchTo(this.data[0].chooseId);
                }
            },
            
        },
    },
    // async created(){
    //     var resp = await getNewsChannels();
    //     // console.log(resp);
    //     this.channels = resp;
    //     //此加载过后 才有频道
    //     this.switchTo(this.channels[0].chooseId);
    // },
    methods:{
        //切换chooseId
        switchTo(id){
            this.chooseId = id;
            //当我切换频道后 触发这个事件
            this.$emit("change",this.chooseId)
        },
    },
}
</script>

<style scoped>
.news-types{
    margin: 30px 0;
    border-bottom: 1px solid #ccc;
    display: flex;
    flex-wrap: wrap;
    font-size: 14px;
    padding-bottom: 15px;
}
.item{
    padding: 10px 20px;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin-right: 15px;
    margin: 7px 15px 7px 0;
    cursor: pointer;
    position: relative;
}
.item:hover{
    background: #ccc;
}
.item.active{
    background: #000;
    color: #fff;
}
.news-types a{
    align-self: flex-end;
    margin-bottom: 20px;
    color: #409eff;
    
}

</style>