<template>
    <div>
        <Banner :banners = "banners"  :duration = "4000"/>
        <Channels @change="handleChange" />
        <Loading v-show="isLoading"/>
        <NewsList v-show="!isLoading" :news="news" />
    </div>
</template>

<script>
import Banner from "../components/Banner";
import Loading from "../components/Loading";
import Channels from "../components/mews/Channels";
import NewsList from "../components/mews/NewsList";
import {getNews} from '../services/newService';

export default {
    components:{
        Banner,
        Loading,
        Channels,
        NewsList,
        
    },

    data(){
        return {
            banners :[
                {link: "https://www.baidu.com", url: require("../assets/img/ceshi.jpg")},
                {link: "https://www.baidu.com", url: require("../assets/img/ceshi.jpg")},
                {link: "https://www.baidu.com", url: require("../assets/img/ceshi.jpg")},
                
            ],
            news: [],
            isLoading:true
        }
    },

    methods:{
        async handleChange(channelId){
            this.isLoading = true;
            var resp = await getNews(channelId, 1., 10);
            this.news = resp.contentlist;
            this.isLoading = false;
        },
    },  
};
</script>

