<template>
    <div class="banner-container" @mouseenter="autoStop" @mouseleave="autoStart">
        <ul class="images" :style="{width: banners.length * 100 + '%',marginLeft: -index * 100 + '%'}">
            <li v-for="(item, i) in banners" :key="i">
                <a :href="item.link"><img :src="item.url" alt=""/></a>
            </li>
        </ul>
        <ul class="dost">
            <li v-for="(item, i) in banners" :key="i" :class="{active: i === index,}" @click="index=i"></li>
        </ul>
    </div>
</template>

<script>
export default {
    props: {
        banners: {
            type: Array,//属性类型是数组
            required: true,//必须要传递该属性
        },

        duration: {
            type: Number,
            default: 2000, //默认值
        },
    },
    
   
    //启动
    created(){
        this.autoStart();
    },
    //销毁
    destroyed(){
        this.autoStop();
    },
    data() {
        return {
            index: 0, //当前显示的第几张图片
            timer: null
        }
    },
    methods: {
        //自动切换
        autoStart(){
            if (this.timer){
                return;
            }
            this.timer = setInterval(()=> {
                this.index = (this.index + 1) % this.banners.length;
                }, this.duration);
        },
        //停止切换
        autoStop(){
            clearInterval(this.timer);
            this.timer = null;
        }
    }
}
</script>

<style scoped>
/* 组件样式加上scoped */
    .banner-container{
        height: 350px;
        position: relative;
        overflow: hidden;
    }
    .banner-container li{
        display: block;
        width: 1080px;
        height: 100%;
        float: left;
    }
    .images{
        height: 100%;
        padding: 0%;
        transition: 0.5s;
    }
    .banner-container img {
        width: 1080px;
        height: 100%;
    }
    .dost{
        position: absolute;
        bottom: 10px;
        right: 10px;
        display: flex;
    }
    .dost li{
        width: 10px;
        cursor: pointer;
        height: 10px;
        margin: 0 3px;
        border-radius: 50%;
        border: 1px solid;
        color: #fff;
    }
    .dost .active{
        background: #000;
    }
</style>